body, div, dl, dt, dd, ul, ol, li,
h1, h2, h3, h4, h5, h6, pre, code,
form, fieldset, figure, legend, input, button,
textarea, p, blockquote, th, td, select {
    margin: 0;
    padding: 0;
}
fieldset, img {
    border: 0;
}
/* remember to define focus styles! */
:focus {
    outline: 0;
}
address, i, cite, code, dfn, em, var {
    font-style: normal;
}
b, caption, th {
    font-weight: normal;
}
h1, h2, h3, h4, h5, h6 {
    font-size: 100%;
    font-weight: normal;
}
abbr, acronym {
    border: 0;
    font-variant: normal;
}
input, button, textarea,
optgroup, select, option {
    font-family: inherit;
    *font-family: "\5FAE\8F6F\96C5\9ED1", "Microsoft YaHei";
    font-size: inherit;
    *font-size: 100%;
    font-style: inherit;
    font-weight: inherit;
    color: inherit;
}
code, kbd, samp, tt, big, small {
    font-size: 100%;
}
body {
    line-height: 1.5;
    font: 12px "\5FAE\8F6F\96C5\9ED1", "Microsoft YaHei", Tahoma, Helvetica, Arial, sans-serif;
    color: #333;
}
ol, ul {
    list-style: none;
}
/* tables still need 'cellspacing="0"' in the markup */
table {
    border-collapse: collapse;
    border-spacing: 0;
}
caption, th {
    text-align: left;
}
sup, sub {
    font-size: 100%;
    vertical-align: baseline;
}
/* remember to highlight anchors and inserts somehow! */
:link, :visited {
    text-decoration: none;
}
a,a:link,a:active,a:visited {
	color: #333;
	text-decoration:none;
	/* border:0 none; */
	/* background:none; */
	outline:none;
}
a:hover {
    color: #333;
}

a
{
    transition-property: border-color, color, background-color;
    transition-duration: .2s;
}

blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}

button, [type="submit"] {
    cursor: pointer;
}


